body {
  background-color: azure;
}

.container {
  max-width: 1200px;
  border: 8px double #4a86ac;
  margin: 90px auto;
  padding: 20px;
}

.list {
  width: 25%;
  height: auto;
  padding-bottom: 20px;
}

.hidden {
  display: none;
}

.cat-info img {
  width: 400px;
  height: 300px;
  border: 5px double #4a86ac;
  margin-left: 30%;
}

.loader-container {
  min-height: 30vh;
  width: 30vh;
  background-color: #4a86ac;
  border-radius: 40%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.loader-animation {
  color: #fff;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  background-color: currentColor;
  border-radius: 50%;
  font-size: 48px;
  position: relative;
  transform: rotateX(-60deg)perspective(1000px);
}

.loader-animation:before, .loader-animation:after {
  content: "";
  box-sizing: border-box;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  animation: flowerFlow 1s ease-out infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-animation:after {
  animation-delay: .4s;
}

@keyframes flowerFlow {
  0% {
    opacity: 1;
    transform: rotate(0deg);
    box-shadow: 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em;
  }

  100% {
    opacity: 0;
    transform: rotate(180deg);
    box-shadow: -1em -1em 0 -.35em, 0 -1.5em 0 -.35em, 1em -1em 0 -.35em, -1.5em 0 0 -.35em, 1.5em 0 0 -.35em, -1em 1em 0 -.35em, 0 1.5em 0 -.35em, 1em 1em 0 -.35em;
  }
}

.error-text {
  color: #863939;
  text-align: center;
  padding: 10px;
}

.error-container {
  letter-spacing: 1px;
  color: #fff;
  text-shadow: 0 0 1px #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 35px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
}

.error-container:before {
  content: "Use key F5";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

